import React from 'react';
import PropTypes from 'prop-types';

import styles from '../UserList.module.css';

import WatchedFilm from './WatchedFilm/WatchedFilm';

const WatchedList = ({ watchedList, deleteFromWatched }) => {
  return (
    <section className={styles.filmsSection}>
      {watchedList.length > 0 ? (
        <ul className={styles.filmsUl}>
          {watchedList.map(film => (
            <li key={film.id} className={styles.filmItem}>
              <WatchedFilm film={film} deleteFromWatched={deleteFromWatched} />
            </li>
          ))}
        </ul>
      ) : (
        <div className={styles.emptyFilmsDiv}>
          This folder is empty. <br /> Save any film you have watched to start!
        </div>
      )}
    </section>
  );
};
WatchedList.propTypes = {
  watchedList: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
};
export default WatchedList;
