import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import styles from '../../UserList.module.css';
import { ReactComponent as Busket } from '../../busket.svg';

const WatchedFilm = ({ film, deleteFromWatched }) => {
  return (
    <div className={styles.mainWrapper}>
      <section className={styles.filmItemWrapper}>
        <img
          className={styles.img}
          src={
            film.poster_path
              ? `https://image.tmdb.org/t/p/w200/${film.poster_path}`
              : 'https://www.viser.edu.rs/images/no_photo.jpeg'
          }
          alt="film poster"
        />
      </section>{' '}
      <h3 className={styles.movieName}> {film.title} </h3>{' '}
      <p className={styles.movieName}> {film.release_date} </p>{' '}
      <button
        type="button"
        title="delete from list"
        onClick={() => deleteFromWatched(film.id)}
        className={styles.deleteButton}
      >
        <Busket className={styles.busketSvg} />{' '}
      </button>{' '}
      <NavLink to={`/${film.title}/${film.id}`}>
        <button type="button" className={styles.showMoreButton}>
          show more{' '}
        </button>{' '}
      </NavLink>{' '}
    </div>
  );
};
WatchedFilm.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    poster_path: PropTypes.string.isRequired,
    release_date: PropTypes.string.isRequired,
  }).isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
};
export default WatchedFilm;
